.app__works {
    flex: 1;
    width: 100%;
    flex-direction: column;
  }

  .app__work-filter {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;

    margin: 4rem 0 2rem;

    &-item {
      padding: 0.5rem 1rem;
      border-radius: 0.5rem;
      background-color: #fff;
      color: #000;
      font-weight: 800;

      cursor: pointer;
      transition: all 0.3s ease;
      margin: 0.5rem;

      &:hover {
        background-color: var(--secondary-color);
        color: #fff;
      }

      @media screen and (min-width: 2000px) {
        padding: 1rem 2rem;
        border-radius: 0.85rem;
      }
    }

    .item-active {
      background-color: var(--secondary-color);
      color: #fff;
    }
  }

  .app__work-portfolio {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    
  }

  .app__work-item {
    width: auto;
    max-width: 400px;
    height: auto;


    flex-direction: column;

    margin-right: auto;
    margin-left: auto;
    padding: 0s;
    border-radius: 0.5rem;
    background-color: #fff;
    color: #000;

    cursor: pointer;
    transition: all 0.3s ease;

    @media screen and (min-width: 2000px) {
      max-width: 470px;
      width: auto;
      padding: 1.25rem;
      border-radius: 0.75rem;
    }

    @media screen and (max-width: 300px) {
      width: 200px;
      margin: 1rem;
    }

    &-img{
      width: auto;
      height: auto;
      margin: 0;
 
      img{
        width: auto;
        height: auto;
        border-radius: 0.5rem;
      }
    }

    &:hover {
      box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
    }

    
  }

  .app__work-img {
    width: 100%;
    height: 230px;

    position: relative;

    img {
      width: 100%;
      height: 100%;
      border-radius: 0.5rem;
      object-fit: cover;
    }

    @media screen and (min-width: 2000px) {
      height: 350px;
    }
  }

  .app__work-hover {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);

    border-radius: 0.5rem;
    opacity: 0;
    transition: all 0.3s ease;

    div {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background-color: rgba(0, 0, 0, 0.5);
      color: #fff;

      margin: 1rem;
      font-family: var(--font-base);
      font-weight: 800;
      cursor: pointer;
      transition: all 0.3s ease;

      svg {
        width: 50%;
        height: 50%;
        color: var(--white-color);
      }
    }
  }

  .app__work-content {
    padding: 0.5rem;
    width: auto;
    position: relative;
    flex-direction: column;

    h4 {
      margin-top: 1rem;
      line-height: 1.5;

     
    }

    .app__work-tag {
      margin: 3px;
      padding: 0.5rem 1rem;
      border-radius: 10px;
      background-color: rgb(202, 42, 2);
      color: white;

    }
  }

  .project{
    flex-direction: column;
    border: #000 solid 1px;
    margin: 10px;
    border-radius: 20px;
    padding: 10px;
    width: auto;

    img{
        max-width: 300px;
        width: auto;
        height: auto;
    }
  }

  .project__container{
  width: auto;
  height: auto;

    display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;

  @media (max-width: 900px) {
    grid-template-columns: repeat(1, 1fr);
  }
  }
